import DashboardLayout from '@/views/Layout/DashboardLayout.vue';
import AuthLayout from '@/views/Pages/AuthLayout.vue';

// GeneralViews
import NotFound from '@/views/GeneralViews/NotFoundPage.vue';

// Dashboard pages
const Dashboard = () => import(/* webpackChunkName: "dashboard" */ '@/views/Dashboard/Dashboard.vue');

// Products
const ProductsIndex = () => import(/* webpackChunkName: "products" */ '@/cruds/products/Index.vue');
const ProductsCreate = () => import(/* webpackChunkName: "products/create" */ '@/cruds/products/Create.vue');
const ProductsEdit = () => import(/* webpackChunkName: "products/create" */ '@/cruds/products/Edit.vue');
const ProductsGrid = () => import(/* webpackChunkName: "products/create" */ '@/cruds/products/Grid.vue');

// Tables
const TablesIndex = () => import(/* webpackChunkName: "tables" */ '@/cruds/tables/Index.vue');
const TablesCreate = () => import(/* webpackChunkName: "tables/create" */ '@/cruds/tables/Create.vue');
const TablesEdit = () => import(/* webpackChunkName: "tables/create" */ '@/cruds/tables/Edit.vue');
const TablesGrid = () => import(/* webpackChunkName: "tables/create" */ '@/cruds/tables/Grid.vue');

// Pages
const User = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/UserProfile.vue');
const Pricing = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/Pricing.vue');
const TimeLine = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/TimeLinePage.vue');
const Login = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/Login.vue');
const Home = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/Home.vue');
const Register = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/Register.vue');
const Lock = () => import(/* webpackChunkName: "pages" */ '@/views/Pages/Lock.vue');

let pagesMenu = {
  path: '/pages',
  component: DashboardLayout,
  name: 'pages',
  redirect: '/pages/user',
  children: [
    {
      path: 'user',
      name: 'user',
      component: User
    },
    {
      path: 'timeline',
      name: 'timeline',
      component: TimeLine
    }
  ]
};

let authPages = {
  path: '/',
  component: AuthLayout,
  name: 'Authentication',
  children: [
    {
      path: '/home',
      name: 'home',
      component: Home,
      meta: {
        noBodyBackground: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/pricing',
      name: 'pricing',
      component: Pricing
    },
    {
      path: '/lock',
      name: 'lock',
      component: Lock
    },
    { path: '*', component: NotFound }
  ]
};

const routes = [
  {
    path: '/',
    redirect: '/dashboard',
    name: 'dashboard_layout'
  },
  pagesMenu,
  {
    path: '/',
    component: DashboardLayout,
    redirect: '/dashboard',
    name: 'home',
    children: [
      {
        path: 'dashboard',
        name: 'dashboard',
        component: Dashboard
      },
      {
        path: 'products',
        name: 'products.index',
        component: ProductsIndex
      },
      {
        path: 'products/grid',
        name: 'products.grid',
        component: ProductsGrid
      },
      {
        path: 'products/create',
        name: 'products.create',
        component: ProductsCreate
      },
      {
        path: 'products/:id/edit',
        name: 'products.edit',
        component: ProductsEdit
      },
      {
        path: 'tables',
        name: 'tables.index',
        component: TablesIndex
      },
      {
        path: 'tables/grid',
        name: 'tables.grid',
        component: TablesGrid
      },
      {
        path: 'tables/create',
        name: 'tables.create',
        component: TablesCreate
      },
      {
        path: 'tables/:id/edit',
        name: 'tables.edit',
        component: TablesEdit
      }
    ]
  },
  authPages,
];

export default routes;
