const FilterService = {
  setLinearFilters: (filters, params) => {
    return _.map(filters, function (o) {
      if (o.column == params.column) {
        return {
          column: o.column,
          operator: o.operator,
          query_1: params.query_1
        };
      } else {
        return o;
      }
    });
  },
  setCoupleFilters: (filters, params) => {
    return _.map(filters, function (o) {
      if (o.column == params.column) {
        return {
          column: o.column,
          column_2: o.column_2,
          operator: o.operator,
          query_1: params.query_1,
          query_2: params.query_2
        };
      } else {
        return o;
      }
    });
  },
  rejectEmptyFilters: (params) => {
    return _.reject(params, function (o) {
      return o.query_1 === "";
    });
  }
};

export default FilterService;
