<template>
  <div class="sub-header">
    <nav class="slidemenu">
      <!-- Item 1 -->
      <input type="radio" name="slideItem" id="slide-item-1" class="slide-toggle" value="food" @input="changeTab"
        checked />
      <label for="slide-item-1">
        <p class="icon"></p>
        <span>{{ $t('cruds.products.category.food') }}</span>
      </label>

      <!-- Item 2 -->
      <input type="radio" name="slideItem" id="slide-item-2" class="slide-toggle" value="drink" @input="changeTab" />
      <label for="slide-item-2">
        <p class="icon"></p>
        <span>{{ $t('cruds.products.category.drink') }}</span>
      </label>

      <div class="clear"></div>

      <!-- Bar -->
      <div class="slider">
        <div class="bar"></div>
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  methods: {
    changeTab(e) {
      this.$eventHub.$emit("changed-type", e.target.value);
    }
  }
}
</script>

<style scoped >
* {
  margin: 0;
  padding: 0;
}

.sub-header {
  margin-top: 52px;
}

.clear {
  clear: both;
}

.slide-toggle {
  display: none;
}

.slidemenu {
  font-family: arial, sans-serif;
  max-width: 100%;
  overflow: hidden;
}

.slidemenu label {
  width: 50%;
  text-align: center;
  display: block;
  float: left;
  color: #333;
  opacity: 0.5;
}

.slidemenu label:hover {
  cursor: pointer;
  color: #be1414;
}

.slidemenu label span {
  display: block;
  padding: 8px;
  margin-top: -20px;
  margin-bottom: -10px;
  font-size: 12px;
}

.slidemenu label .icon {
  font-size: 30px;
  text-align: center;
  height: 50px;
  width: 50px;
  display: block;
  margin: 0 auto;
  line-height: 50px;
}

/*Bar Style*/
.slider {
  width: 100%;
  height: 5px;
  display: block;
  background: #ccc;
  margin-top: 10px;
  border-radius: 5px;
}

.slider .bar {
  width: 50%;
  height: 5px;
  background: #ff2929;
  border-radius: 5px;
}

/*Animations*/
.slidemenu label,
.slider .bar {
  transition: all 500ms ease-in-out;
  -webkit-transition: all 500ms ease-in-out;
  -moz-transition: all 500ms ease-in-out;
  margin-top: -10px;
}

/*Toggle*/
.slidemenu .slide-toggle:checked+label {
  opacity: 1;
  color: red;
}

.slidemenu #slide-item-1:checked~.slider .bar {
  color: red;
  margin-left: 0;
}

.slidemenu #slide-item-2:checked~.slider .bar {
  margin-left: 50%;
}</style>