<template>
  <div></div>
</template>

<script>
export default {
  created() {
    this.$eventHub.$on("create-success", this.itemCreated);
    this.$eventHub.$on("update-success", this.itemUpdated);
    this.$eventHub.$on("delete-success", this.itemDeleted);
  },
  methods: {
    itemCreated() {
      this.$swal.fire({
        toast: true,
        position: 'top-right',
        iconColor: 'white',
        customClass: {
          popup: 'colored-toast'
        },
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        icon: 'success',
        title: this.$t("components.event_hub_item_created")
      })
    },
    itemUpdated() {
      this.$swal.fire({
        toast: true,
        position: 'top-right',
        iconColor: 'black',
        customClass: {
          popup: 'colored-toast'
        },
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        icon: 'info',
        title: this.$t("components.event_hub_item_updated")
      })
    },
    itemDeleted() {
      this.$swal.fire({
        toast: true,
        position: 'top-right',
        iconColor: 'white',
        customClass: {
          popup: 'colored-toast'
        },
        showConfirmButton: false,
        timer: 1500,
        timerProgressBar: true,
        icon: 'error',
        title: this.$t("components.event_hub_item_deleted")
      })
    },
  },
  watch: {
    $route: {
      handler() {
        axios.get("http://192.168.100.51:8000/abilities").then((response) => {
          this.$ability.update([
            { subject: "all", actions: response.data },
          ]);
        });
      },
      immediate: true,
    },
  },
};
</script>