function initialState() {
  return {
    entry: {
      id: null,
      name: "",
      description: "",
      type: "",
      price: "",
      category_id: "",
      image: "",
      path: ""
    },
    lists: {
      categories: []
    },
    loading: false
  };
}

const route = "products";

const getters = {
  entry: state => state.entry,
  loading: state => state.loading,
  lists: state => state.lists
};

const actions = {
  storeData({ commit, state, dispatch }) {
    commit("setLoading", true);

    return new Promise((resolve, reject) => {
      let params = objectToFormData(state.entry, {
        indices: true,
        booleansAsIntegers: true
      });
      axios
        .post(route, params)
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          let message = error.response.data.message || error.message;
          let errors = error.response.data.errors;

          reject(error);
        })
        .finally(() => {
          commit("setLoading", false);
        });
    });
  },
  updateData({ commit, state, dispatch }) {
    commit("setLoading", true);

    return new Promise((resolve, reject) => {
      let params = objectToFormData(state.entry, {
        indices: true,
        booleansAsIntegers: true
      });
      params.set("_method", "PUT");
      axios
        .post(`${route}/${state.entry.id}`, params)
        .then(response => {
          resolve(response);
        })
        .catch(error => {
          let message = error.response.data.message || error.message;
          let errors = error.response.data.errors;
          
          reject(error);
        })
        .finally(() => {
          commit("setLoading", false);
        });
    });
  },
  setName({ commit }, value) {
    commit("setName", value);
  },
  setDescription({ commit }, value) {
    commit("setDescription", value);
  },
  setType({ commit }, value) {
    commit("setType", value);
  },
  setPrice({ commit }, value) {
    commit("setPrice", value);
  },
  setImage({ commit }, value) {
    commit("setImage", value);
  },
  setCategoryId({ commit }, value) {
    commit("setCategoryId", value);
  },
  fetchCreateData({ commit, dispatch }, id) {
    commit("setLoading", true);
    axios.get(`${route}/create`).then(response => {
      commit("setLists", response.data.meta);
      commit("setLoading", false);
    });
  },
  fetchEditData({ commit, dispatch }, id) {
    commit("setLoading", true);
    axios.get(`${route}/${id}/edit`).then(response => {
      commit("setEntry", response.data.data);
      commit("setLists", response.data.meta);
      commit("setLoading", false);
    });
  },
  fetchShowData({ commit, dispatch }, id) {
    axios.get(`${route}/${id}`).then(response => {
      commit("setEntry", response.data.data);
    });
  },
  fetchCategoriesByTypeData({ commit, dispatch }, type) {
    let params = {
      type: type
    };

    commit("setLoading", true);
    axios.get(`categories-by-type`, { params }).then(response => {
      commit("setCategoriesLists", response.data.meta.categories);
      commit("setLoading", false);
    });
  },
  resetState({ commit }) {
    commit("resetState");
  }
};

const mutations = {
  setEntry(state, entry) {
    state.entry = entry;
  },
  setName(state, value) {
    state.entry.name = value;
  },
  setDescription(state, value) {
    state.entry.description = value;
  },
  setType(state, value) {
    state.entry.type = value;
  },
  setPrice(state, value) {
    state.entry.price = value
  },
  setImage(state, value) {
    console.log(value);
    state.entry.path = value;
    state.entry.image = value;
  },
  setCategoryId(state, value) {
    state.entry.category_id = value;
  },
  setLoading(state, loading) {
    state.loading = loading;
  },
  resetState(state) {
    state = Object.assign(state, initialState());
  },
  setCategoriesLists(state, categories) {
    state.lists.categories = categories;
  },
  setLists(state, lists) {
    state.lists = lists;
  }
};

export default {
  namespaced: true,
  state: initialState,
  getters,
  actions,
  mutations
};
