const GlobalMixins = {
  install(Vue) {
    Vue.mixin({
      methods: {
        $formatDate: function (value) {
          if (!value) {
            return "";
          }
          return moment(String(value)).format("YYYY-MM-DD HH:mm:ss");
        },
        $formatDateHuman: function (value) {
          if (!value) {
            return "";
          }
          return moment(String(value)).format("DD/MM/YY \\s HH:mm:ss");
        },
        $extractNumber: function (value) {
          return value.replace(/\D+/g, m => m == ',' ? '.' : '');
        },
        $getViewPath: function (entity) {
          if (localStorage.getItem(entity))
            return `/${entity}${localStorage.getItem(entity)}`
          return `/${entity}`
        },
        $setViewPath: function (entity, path) {
          localStorage.setItem(entity, path)
        },
        $maskedValue: function (value, contry) {
          return parseFloat(value)
            .toLocaleString(contry.language, { style: 'currency', currency: contry.currency });
        },
        $limitCharText: function (value, limit) {
          return value.substring(0, limit) + "..."
        }
      },
    })
  }
}

export default GlobalMixins
