<template>
  <div class="loader-container" :style="styleCustom">
    <div class="animated-background"></div>
  </div>
</template>

<script>
export default {
  props: [
    "width_custom",
    "height_custom",
    "border_radius",
    "margin_left_custom",
    "margin_right_custom"
  ],
  computed: {
    styleCustom() {
      return {
        "--height_custom": this.height_custom,
        "--width_custom": this.width_custom,
        "--border_radius": this.border_radius,
        "--margin_left_custom": this.margin_left_custom,
        "--margin_right_custom": this.margin_right_custom
      };
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.loader-container {
  width: var(--width_custom);
  margin-right: var(--margin_right_custom);
  margin-left: var(--margin_left_custom);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;
  border-radius: 50%;
}

@keyframes placeholder {
  0% {
    background-position: -600px 0;
  }

  100% {
    background-position: 600px 0;
  }
}

.animated-background {
  height: var(--height_custom);
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeholder;
  animation-timing-function: linear;
  background: #eeeeee;
  background: linear-gradient(to right, #fff -5%, #f4f4f4 18%, #fbfbfb 34%);
  background-size: 1200px 100px;
  min-height: 30px;
  width: 100%;
  margin: 5px 0 5px 0;
  border-radius: var(--border_radius);
}

.profile-image {
  margin-top: 10px;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}
</style>
