import FilterService from "@/services/filterService";
const set = key => (state, val) => {
  state[key] = val;
};

function getDefaultState() {
  return {
    data: [],
    total: 0,
    query: {},
    loading: false,
    lists: {},
    filters: [
      { column: "id", operator: "equals", query_1: "" },
      { column: "area_id", operator: "equals", query_1: "" },
      { column: "status", operator: "contains", query_1: "" }
    ]
  };
}

const getters = {
  data: state => state.data,
  total: state => state.total,
  lists: state => state.lists,
  loading: state => state.loading
};

const actions = {
  fetchIndexData({ commit, state, dispatch }, type) {
    commit("setLoading", true);

    const filters = FilterService.rejectEmptyFilters(state.filters);

    const queryParams = {
      ...state.query,
      ...{ filters: filters }
    };

    axios
      .get("tables", { params: queryParams })
      .then(response => {
        commit("setLists", response.data.meta);
        commit("setData", response.data.data.data);
        commit("setTotal", response.data.data.total);
      })
      .catch(error => {
        message = error.response.data.message || error.message;
      })
      .finally(() => {
        commit("setLoading", false);
      });
  },
  destroyData({ commit, state, dispatch }, id) {
    axios
      .delete("tables/" + id)
      .then(response => {
        dispatch("fetchIndexData");
      })
      .catch(error => {
        message = error.response.data.message || error.message;
      });
  },
  setQuery({ commit }, value) {
    commit("setQuery", _.cloneDeep(value));
  },
  setFilter({ commit }, value) {
    commit("setFilter", value);
  },
  setLinearFilters({ commit }, value) {
    commit("setLinearFilters", value);
  },
  resetState({ commit }) {
    commit("resetState");
  }
};

const mutations = {
  setData: set("data"),
  setTotal: set("total"),
  setQuery: set("query"),
  setLoading: set("loading"),
  setLists: set("lists"),
  setLinearFilters(state, value) {
    state.filters = FilterService.setLinearFilters(state.filters, value);
  },
  resetState(state) {
    Object.assign(state, getDefaultState());
  }
};

export default {
  namespaced: true,
  state: getDefaultState,
  getters,
  actions,
  mutations
};
