<template>
  <nav>
    <ul>
      <li v-for="(item, index) in options.data_nav" :key="item.id">
        <a :href="`#${item.id}`" :id="`nav-${item.id}`"
          :class="{ 'active': !index, 'food': options.type == 'food', 'drink': options.type == 'drink' }">{{ item.label
          }}</a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ['options'],
  mounted() {
    this.initalize()
  },
  methods: {
    initalize() {
      var sections = $('section')
        , nav = $('nav')
        , nav_height = nav.outerHeight();

      $(window).on('scroll', function () {
        var cur_pos = $(this).scrollTop();

        sections.each(function () {
          var top = $(this).offset().top - nav_height - 135,
            bottom = top + $(this).outerHeight();

          if (cur_pos >= top && cur_pos <= bottom) {
            nav.find('a').removeClass('active');
            sections.removeClass('active');

            // Navega o item do menu da seo atual.
            let idanav = $(this).attr('id');
            if (idanav) {
              document.getElementById(`nav-${idanav}`).scrollIntoView({
                behavior: 'smooth',
                block: 'center',
                inline: 'center'
              })
            }

            $(this).addClass('active');
            nav.find('a[href="#' + idanav + '"]').addClass('active');
          }
        });
      });

      nav.find('a').on('click', function (e) {
        e.preventDefault();

        var $el = $(this)
          , id = $el.attr('href');

        $('html, body').animate({
          scrollTop: $(id).offset().top - nav_height - 120
        }, 200);

        return false;
      });
    }
  },
};
</script>

<style scoped>
/* Navigation */
nav {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 100px;
  height: 42px;
  background: #ffffff;
  z-index: 1;
}

nav ul {
  margin: 0 auto;
  list-style: none;
  text-align: center;
  max-width: 99%;
  width: 100%;
  overflow-y: auto;
  display: flex;
  padding-left: 0;
  background: #ffffff;
  margin-top: 8px;
}

nav ul::-webkit-scrollbar {
  display: none;
}

nav ul li {
  display: inline-block;
  margin: 0 10px;
}

nav ul li a {
  color: #000;
  font-size: 14px;
  text-decoration: none;
  transition: all 0.2s ease;
}

a.active {
  color: red;
  border-bottom: 1px solid red;
}

h1 {
  font-size: 5rem;
  color: #34495E;
}

section:nth-child(even) {
  background: #ecf0f1;
}

section:nth-child(odd) {
  background: #bdc3c7;
}
</style>

