import { abilitiesPlugin } from "@casl/vue";
import ability from "./config/casl/ability";
import VueSweetalert2 from "vue-sweetalert2";
import BaseInput from '@/components/Inputs/BaseInput.vue';
import BaseDropdown from '@/components/BaseDropdown.vue';
import Card from '@/components/Cards/Card.vue';
import BaseButton from '@/components/BaseButton.vue';
import RouteBreadcrumb from '@/components/Breadcrumb/RouteBreadcrumb';
import BasePagination from "@/components/BasePagination";
import BaseNav from "@/components/Navbar/BaseNav";
import BaseHeader from '@/components/BaseHeader';
import { ValidationProvider, ValidationObserver } from 'vee-validate';
import { Select, Option } from 'element-ui';
import SwDatatable from '@/components/Datatables/Datatable';
import FormLoader from '@/components/FormLoader';
import DropzoneFileUpload from '@/components/Inputs/DropzoneFileUpload'
import TypeTab from '@/components/Home/TypeTab.vue'
import ContentDetail from '@/components/Home/ContentDetail.vue'
import ContentSpinner from '@/components/Home/ContentSpinner.vue'

/**
 * You can register global components here and use them as a plugin in your main Vue instance
 */

const GlobalComponents = {
  install(Vue) {
    Vue.use(abilitiesPlugin, ability);
    Vue.use(VueSweetalert2);
    Vue.component('base-button', BaseButton);
    Vue.component('base-header', BaseHeader);
    Vue.component('base-input', BaseInput);
    Vue.component('base-dropdown', BaseDropdown);
    Vue.component('base-nav', BaseNav);
    Vue.component('base-pagination', BasePagination);
    Vue.component('card', Card);  
    Vue.component('route-bread-crumb', RouteBreadcrumb);
    Vue.component('validation-provider', ValidationProvider)
    Vue.component('validation-observer', ValidationObserver)
    Vue.component('sw-datatable', SwDatatable);
    Vue.component('form-loader', FormLoader);
    Vue.component('dropzone-file-upload', DropzoneFileUpload);
    Vue.component('el-select', Select);
    Vue.component('el-option', Option);
    Vue.component('sw-type-tab', TypeTab);
    Vue.component('sw-content-detail', ContentDetail);
    Vue.component('sw-content-spinner', ContentSpinner);
  }
};

export default GlobalComponents;
