<template>
  <div class="spin-content">
    <button @click="increment" class="btn btn-danger btn-red btn-sm spin-left">+</button>
    <span>{{ targetValue }}</span>
    <button  @click="decrement" class="btn btn-danger btn-red btn-sm spin-right">-</button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      targetValue: 0
    }
  },
  methods: {
    increment() {
      this.targetValue++
    },
    decrement() {
      if (this.targetValue) {
        this.targetValue--
      }
    }
  },
}
</script>
<style scoped>
.spin-content {
  display: flex;
}

.spin-left {
  min-width: 25px;
  max-height: 25px;
  margin-right: 2px;
  margin-left: -4px;
}

.spin-right {
  min-width: 25px;
  max-height: 25px;
  margin-left: 7px;
}
.btn-red {
  background-color: red;
}
.btn-red:focus {
  background-color: red;
}
</style>