<template>
  <div>
    <el-table :data="tableData" row-key="id" header-row-class-name="thead-light" size="small"
      :cell-style="{ 'padding': '0', 'padding-left': '23px' }">
      <el-table-column v-for="column in tableColumns" :key="column.label" v-bind="column">
        <template v-slot="{ row, column }">
          <component v-if="tableColumns[column.property].component" :is="tableColumns[column.property].component"
            :row="row" :property="column.property" :rowOptions="tableColumns[column.property]" />

          <span v-else>{{ row[column.property] }}</span>
        </template>
      </el-table-column>

      <el-table-column min-width="100px" :label="$t('general.actions')">
        <div slot-scope="{$index, row}" class="d-flex">
          <a @click="handleEdit($index, row)" class="edit-button btn-just-icon text-default">
            <i class="material-icons">edit</i>
          </a>
          <a @click="handleDelete($index, row)" class="remove-button btn-just-icon text-red">
            <i class="material-icons red-text">delete</i>
          </a>
        </div>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { Table, TableColumn } from 'element-ui';

export default {
  props: ['tableColumns', 'tableData', 'xprops'],
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  methods: {
    handleEdit(index, row) {
      this.$router.push({ path: `/${this.xprops.route}/${row.id}/edit` });
    },
    handleDelete(index, row) {
      this.$swal.fire({
        title: this.$t('general.title_modal_delete'),
        text: this.$t('general.subtitle_modal_delete'),
        type: 'warning',
        showCancelButton: true,
        confirmButtonClass: 'btn btn-success btn-fill',
        cancelButtonClass: 'btn btn-danger btn-fill',
        confirmButtonText: this.$t('general.delete'),
        cancelButtonText: this.$t('general.cancel'),
        buttonsStyling: false
      }).then(result => {
        if (result.value) {
          this.deleteRow(row)
          this.$swal.fire({
            title: this.$t('general.excluded'),
            text: `${this.$t('general.you_removed')} ${row.id}`,
            type: 'success',
            confirmButtonClass: 'btn btn-success btn-fill',
            buttonsStyling: false
          });
        }
      });
    },
    deleteRow(row) {
      this.$emit('delete', row);
    }
  }
};
</script>

<style scoped>
.edit-button {
  cursor: pointer;
}

.remove-button {
  cursor: pointer;
}
</style>